.section-noticias {
  padding-top: 4rem;
  padding-bottom: 4rem;
  background-color: $white;
  text-align: center;
  @media (min-width: 992px) {
    padding-top: 4rem;
  }
  a {
    text-decoration: none;
    img {
      width: 40%;
      border-radius: 15px;
      transition: all .5s;
      @media (min-width: 992px) {
        width: 15%;
      }
      &:hover {
        box-shadow: 0px 0px 16px 9px rgba(228, 0, 43, 0.1);
        transform: scale(1.05);
      }
    }
  }
}