.section-productos {
  padding-top: 3rem;
  padding-bottom: 3rem;
  text-align: center;
  background-color: #efeff7;
  .slider-productos {
    width: 80%;
    margin: 3rem auto;
    @media (min-width: 1024px) {
      width: calc(100% - 60px);
    }
    .items-productos {
      .slick-slide {
        text-align: center;
      }
      .slick-next {
        width: 30px;
        height: 30px;
        right: -30px;
        opacity: 0.6;
        transition: all 0.3s;
        img {
          width: 30px;
          height: 30px;
        }
        &:hover {
          opacity: 1;
        }
      }
      .slick-prev {
        left: -30px;
        width: 30px;
        height: 30px;
        opacity: 0.6;
        transition: all 0.3s;
        z-index: 1;
        img {
          width: 30px;
          height: 30px;
        }
        &:hover {
          opacity: 1;
        }
      }
      .slick-next:before,
      .slick-prev:before {
        display: none;
      }
      span {
        font-weight: bold;
      }
    }
    .tab {
      .tablinks {
        flex: 20%;
        background-color: inherit;
        float: left;
        border: none;
        outline: none;
        cursor: pointer;
        padding: 0.5rem 0.5rem 0 0;
        transition: all 0.3s;
        width: 100%;
        font-weight: bold;
        font-size: 1.2rem;
        color: $text-gray;
        &:hover {
          color: $red;
        }
        &.active {
          color: $red;
        }
      }
    }
  }
  .tabcontent {
    display: none;
    margin-top: 4rem;
    -webkit-animation: fadeIn 1s;
    animation: fadeIn 1s;
    .card {
      padding: 1rem;
      border-top-left-radius: 50px;
      border-top-right-radius: 10px;
      border-bottom-right-radius: 50px;
      border-bottom-left-radius: 10px;
      transition: all .5s;
      @media (max-width: 525px) {
        padding: .5rem;
      }
      @media (min-width: 768px) {
        margin: 1rem;
      }
      @media (min-width: 1200px) {
        border-top-left-radius: 70px;
        border-top-right-radius: 15px;
        border-bottom-right-radius: 70px;
        border-bottom-left-radius: 15px;
      }
      &:hover {
        box-shadow: 0px 0px 16px 9px rgba(228, 0, 43, 0.1);
        transform: scale(1.05);
      }
      .content {
        width: 100%;
        height: 115px;
        @media (min-width: 360px) {
          height: 19vh;
        }
        @media (min-width: 425px) {
          height: 25vh;
        }
        @media (min-width: 768px) {
          height: 30vh;
        }
        @media (min-width: 1200px) {
          height: 35vh;
        }
      }
      .img-producto {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        border-top-left-radius: 35px;
      }
      .card-body {
        .card-title {
          color: $red;
          font-weight: bold;
          font-size: 1.2rem;
        }
        .card-text {
          @media (max-width: 991.98px) {
            font-size: .7rem;
          }
        }
        .btn-rounded {
          border-radius: 5px;
        }
        .btn-redgr-white {
          padding-left: 2rem;
          padding-right: 2rem;
          @media (min-width: 768px) and (max-width: 991.98px) {
            padding-left: 1.5rem;
            padding-right: 1.5rem;
          }
          @media (min-width: 992px) {
            padding-left: 3rem;
            padding-right: 3rem;
          }
        }
      }
    }
  }
  .tabcontent.tab-ppal {
    display: block;
  }
  .content-pagination {
    .pagination {
      display: block;
      .next_link,
      .prev_link {
        margin: 0 1rem;
        color: $red;
        transition: all 0.3s;
        &:hover {
          color: $text-gray;
        }
      }
      .text-pagination {
        padding: 0.5rem 1rem;
        background-color: $red;
        color: $white;
        font-weight: 700;
      }
    }
  }
}