.section-cards {
  background-color: $white;
  padding-top: 4rem;
  padding-bottom: 4rem;
  .card {
    margin: 0 1rem;
  }
  .cards-cuadradas {
    margin-top: 4rem;
    @media (max-width: 991.98px) {
      display: flex;
      flex-wrap: nowrap;
      overflow-x: auto;
      overflow-y: hidden;
      &::-webkit-scrollbar {
        display: none;
      }
    }
    .card {
      border: none;
      border-radius: 10px;
      transition: all .5s;
      box-shadow: 0px 24px 21px -15px rgba(0,0,0,0.2);
      &:hover {
        box-shadow: 0px 0px 16px 9px rgba(228, 0, 43, 0.1);
        transform: scale(1.05);
      }
      .content-img-top {
        width: 100%;
        padding-bottom: 70%;
        position: relative;
        border-radius: 10px;
        img {
          position: absolute;
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center;
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
        }
      }
      .card-body {
        position: relative;
        .btn-redgr-white {
          padding-left: 3rem;
          padding-right: 3rem;
          position: absolute;
          top: 0;
          left: 50%;
          transform: translateX(-50%) translateY(-50%);
          @media (min-width: 992px) and (max-width: 1199.98px) {
            padding-left: 2rem;
            padding-right: 2rem;
          }
        }
        .card-text {
          text-align: center;
          margin-top: 1rem;
        }
      }
    }
  }
}