.section-description {
  background-color: #EFEFEF;
  @media (min-width: 992px) {
    background-color: lightgray;
    margin-top: 3%;
    margin-bottom: 3%;
    background-image: url(../images/home/bg-inf.png);
    background-size: cover;
    background-position: center;
  }
  .content-row {
    margin-bottom: -100px;
    @media (min-width: 992px) {
      margin-bottom: 0;
    }
  }
  .col-izq-descrip {
    order: 2;
    text-align: center;
    transform: translateY(-100px);
    @media (max-width: 991.98px) {
      background-image: url(../images/home/img_1.png);
      height: 300px;
      background-position: center;
      background-size: cover;
    }
    @media (min-width: 992px) {
      order: 1;
      transform: translateY(0);
    }
    .img-col-izq {
      display: none;
      padding: 0 3rem;
      height: 100%;
      @media (min-width: 992px) {
        display: block;
      }
      .img-descrip {
        box-shadow: 0px 24px 21px -15px rgba(0,0,0,0.2);
        @media (min-width: 992px) {
          display: block;
          transform: scale(1.2);
          width: 95%;
          height: 100%;
          margin: 0 auto;
          object-fit: cover;
          object-position: center;
        }
      }
    }
  }
  .col-der-descrip {
    padding-top: 2rem;
    padding-bottom: 2rem;
    display: flex;
    align-items: center;
    order: 1;
    @media (min-width: 992px) {
      order: 2;
    }
    .text-descrip {
      margin: 1rem;
      background-color: white;
      padding: 2rem;
      border-radius: 15px;
      width: 100%;
      margin: 0 auto;
      position: relative;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      @media (min-width: 992px) {
        width: 80%;
      }
      .title-descrip {
        font-size: 2.4rem;
        text-align: center;
        font-weight: bold;
        margin-bottom: 0;
      }
      .subtitle-descrip {
        font-size: 1rem;
        @media (min-width: 992px) and (max-width: 1199.98px) {
          font-size: .8rem;
        }
      }
      .img-comillas {
        position: absolute;
        top: 0;
        right: 0;
        transform: translateY(-50%);
      }
    }
  }
}